import logo from "./logo.svg";

import "animate.css/animate.min.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.scss";
import React, { useEffect, useState } from "react";
import firebase from "./firebase";
import BAUCCTV from "./Components/BAU-CCTV";
import FloodCCTV from "./Components/Flood-CCTV";
import RCTCCTV from "./Components/RCT-CCTV";
//import RespondersVideo from "./Components/RespondersVideo";
import Default from "./Components/Default";
import FloatingMenu from "./Components/floatingMenu";

import {
  getFirestore,
  collection,
  getDocs,
  onSnapshot,
  doc,
  setDoc,
} from "firebase/firestore";

import db from "./firebase";

function App() {
  const [screen, setScreen] = useState("default");

  const callScreen = (value) => {
    console.log("callScreen", value);

    setScreen(value);
  };

  const onAddStuff = async () => {
    console.log("add stuff");
    await setDoc(doc(db, "c4", "screen"), {
      side: "FLOOD",
    });
  };

  useEffect(() => {
    console.log("app init");
    onSnapshot(collection(db, "c4"), (snapshot) => {
      const C4Screen = snapshot.docs.map((doc) => doc.data());

      console.log("data", C4Screen[0].side);

      setScreen(C4Screen[0].side);
    });
  }, []);

  return (
    <div className="App">
      <div className="container-platform">
        {screen == "FLOOD" && <BAUCCTV />}

        {screen == "FLOOD-BAU" && <FloodCCTV />}

        {screen == "C4-NEWS" && <RCTCCTV />}

        {/* {screen == "Responder" && <RespondersVideo />} */}

        {screen == "Default" && <Default />}

        <FloatingMenu callScreen={callScreen} />
      </div>
    </div>
  );
}

export default App;
