import React from "react";
import Video from "./video";
import floodCCTVVideo from "../Assets/Flood-CCTV-videos.mp4";

export default function FloodCCTV() {
  return (
    <Video
      vid={floodCCTVVideo}
      width={1920}
      height={1071}
      loop={true}
      muted={true}
    />
  );
}
