import React, { useState } from "react";
function FloatingMenu(props) {
  const [exapndMenu, setExpandMenu] = useState(false);

  const OpenCloseMenu = () => {
    setExpandMenu(!exapndMenu);
  };

  const switchVideo = (value) => {
    setExpandMenu(!exapndMenu);
    props.callScreen(value);
  };
  return (
    <div className="floating-menu">
      {exapndMenu && (
        <div className="menu animate__animated animate__fadeIn">
          <div className="list-container">
            <div className="list-item" onClick={() => switchVideo("FLOOD")}>
              <div className={"item"}>
                <span>BAU News</span>
              </div>
            </div>

            <div className="list-item" onClick={() => switchVideo("FLOOD-BAU")}>
              <div className={"item"}>
                <span>Flood News</span>
              </div>
            </div>
            <div className="list-item" onClick={() => switchVideo("C4-NEWS")}>
              <div className={"item"}>
                <span>RCT Fire News</span>
              </div>
            </div>
            <div className="list-item" onClick={() => switchVideo("Default")}>
              <div className={"item"}>
                <span>Default (Black Screen)</span>
              </div>
            </div>
          </div>
        </div>
      )}

      <div className="main-button" onClick={OpenCloseMenu}>
        <div
          className={
            "arrow-up " + (exapndMenu ? "C4-icon-hide" : "C4-icon-expand")
          }
        />
      </div>
    </div>
  );
}

export default FloatingMenu;
