import React from "react";
import Video from "./video";
import bauCCTVVideo from "../Assets/BAU-CCTV-videos.mp4";

export default function BAUCCTV() {
  return (
    <Video
      vid={bauCCTVVideo}
      width={1920}
      height={1071}
      loop={true}
      muted={true}
    />
  );
}
